<template>
  <v-container fluid>
    <v-layout>
      <navbar color="grey darken-4" textcolor="white--text"></navbar>

      <v-flex row xs6 md3 lg3 xl3>
        <menuoptions></menuoptions>
      </v-flex>

      <v-container grid-list-md class="mt-5">
        <v-layout row wrap class="mt-3">
          <v-flex xs12>
            <v-card class="text-center" color="" dark>
              <img src="../assets/img/logow.png" width="350" class="mt-5" />
              <v-card-text primary-title class="mt-5">
                <h2 class="">Bienvenido al Sistema de Evaluacin de desempeo</h2>
                <h4 class="grey--text">Clover Wireless</h4>
              </v-card-text>
            </v-card>
          </v-flex>    
          <v-flex xs6 md6 lg2 xl2 class="mt-3">
            <v-card class="pink lighten-1 white--text  display-1 font-weight-bold" >                         
              <span class="fas fa-calendar-alt m-4" > </span> {{g_loginUser.EvalYear}} 
            </v-card>
          </v-flex>
          <v-flex xs6 md6 lg5 xl5 class="mt-3" v-if="g_periods.length >  0">
            <v-card class="cyan darken-3 headline" dark >
              <v-card-title primary class="headline font-weight-bold">Revision de asignacin</v-card-title>            
                <div class="cyan darken-3 headline font-weight-bold  ml-2">{{this.g_periods[0].minDate}} - {{this.g_periods[0].maxDate}} </div> 
            </v-card>
          </v-flex>
          <v-flex xs6 md6 lg5 xl5 class="mt-3" v-if="g_periods.length >  0">
            <v-card class="light-blue darken-3" dark>
              <v-card-title primary class="headline font-weight-bold">Evaluacin</v-card-title>            
              <div class="light-blue darken-3 col-10  headline font-weight-bold">{{this.g_periods[1].minDate}} - {{this.g_periods[1].maxDate}}</div>            
            </v-card>
          </v-flex>
          <v-flex xs6 md6 lg7 xl7 v-if="g_periods.length >  0">
            <v-card class="indigo lighten-1" dark>
              <v-card-title primary class="headline font-weight-bold">Revision RH</v-card-title>          
              <div class="indigo lighten-1 col-10 headline font-weight-bold">{{this.g_periods[2].minDate}} - {{this.g_periods[2].maxDate}}</div> 
            </v-card>
          </v-flex>
          <v-flex xs6 md6 lg5 xl5 v-if="g_periods.length >  0">
            <v-card class="blue accent-4" dark>
              <v-card-title primary class="headline font-weight-bold">Retroalimentacin</v-card-title>            
                <div class="blue accent-4 col-10 headline font-weight-bold">{{this.g_periods[3].minDate}} - {{this.g_periods[3].maxDate}} </div>             
            </v-card>
          </v-flex>

          <v-flex xs12 class="mt-5">
            <div v-if="g_loginUser.isRH == 'true'">
              <v-container fluid>
                <v-toolbar dark color="light-blue darken-4 white--text">
                  <h4><i class="far fa-list-alt mr-3"></i>Escala de distribucin</h4>
                  <v-spacer></v-spacer>
                </v-toolbar>
                <listaempleados from="EVAL" :headers="headersDistribucion"
                  excelname="escaladistribuciontodosempleados.xls" v-if="g_loginUser.Subordinates.length > 0"
                  :list='g_loginUser.Subordinates'>
                </listaempleados>
              </v-container>
            </div>

          </v-flex>

        </v-layout>
      </v-container>
    </v-layout>
  </v-container>
</template>

<script>
  import {
    mapState,
    mapActions,
    mapGetters
  } from 'vuex';

  import menuoptions from '../components/menuoptions.vue'
  import navbar from '../components/navbar.vue'
  import listaempleados from '../components/listaempleados.vue'


  export default {

    name: 'minicio',
    components: {
      menuoptions,
      navbar,
      listaempleados
    },
    data() {
      return {
        lorem: `Lorem ipsum dolor sit amet, mel at clita quando. Te sit oratio vituperatoribus, nam ad ipsum posidonium mediocritatem, explicari dissentiunt cu mea. Repudiare disputationi vim in, mollis iriure nec cu, alienum argumentum ius ad. Pri eu justo aeque torquatos.`,
        headersDistribucion: [{
            text: "",
            type: 'text',
            value: "ValueDefinition",
            resize: true
          }, {
            text: "Puntuacin",
            type: 'text',
            value: "Puntuation",
            resize: true
          },
          {
            text: "Evaluacin",
            type: 'text',
            value: "Score",
            resize: false
          },
          {
            text: "Capacidad Maxima",
            type: 'text',
            value: "Quantity",
            resize: false
          },
          {
            text: "Personas Evaluadas",
            type: 'text',
            value: "Evaluated",
            resize: false
          },
          {
            text: "% de Distribucin",
            type: 'text',
            value: "Percentage",
            resize: false
          },
          {
            text: "Num de Personas",
            type: 'text',
            value: "Quantity",
            resize: false
          }, {
            text: "Nombre Personas",
            type: 'combo',
            value: "distributionByEmployee",
            iconbtn: 'fa fa-users',
            titlecombo: 'PrettyName',
            valuecombo: 'Number',
            action: "action_evaluarEmpleado",
            resize: false

          }
        ]
      }
    },
    computed: {
      ...mapGetters(["g_loginUser", "g_periods"]),     
    },
    methods: {},
    created: function () {
      var esto = this;
      esto.$store.dispatch("s_Loading", {
          value: 0,
          show: true
        }),        
        esto.$store.dispatch('GetDistributionAll');
     
    }
  }

</script>

<style scope>
  a {
    color: #feffff;
    text-decoration: none;
  }

  a:hover {
    color: #f7f898;
  }

  @import '../assets/css/global.css';

  @import '../assets/css/media_query.css';

</style>



